import { Card , Image , Space , Divider , Col , Row }  from 'antd'
import dp from '../images/dp.jpg'
import '../index.css'

const { Meta } = Card
const rowStyle = { marginTop : 12 ,  }
const colStyle = { fontWeight : 'bold' , }

export const Profile = (props) => {
    const {loggedUser} = props
    const name = loggedUser.firstName + ' ' + loggedUser.lastName
    return(
        <Space size={200} style={{}}>
            <Card style={{ width : 600 }} >
                <Divider orientation = "center" > Personal Info </Divider>
                <Row style={rowStyle}>
                    <Col span={6} style={colStyle}> First Name: </Col>
                    <Col span={12}> {loggedUser.firstName} </Col>
                </Row>
                <Row style={rowStyle} >
                    <Col span={6} style={colStyle}> Last Name: </Col>
                    <Col span={12}> {loggedUser.lastName} </Col>
                </Row>
                <Row style={rowStyle} >
                    <Col span={6} style={colStyle}> UserName: </Col>
                    <Col span={12}> {loggedUser.userName} </Col>
                </Row>
                <Row style={rowStyle} >
                    <Col span={6} style={colStyle}> Department: </Col>
                    <Col span={12}> {loggedUser.department} </Col>
                </Row>
                <Row style={rowStyle} >
                    <Col span={6} style={colStyle}> Position: </Col>
                    <Col span={12}> {loggedUser.position} </Col>
                </Row>
                <Row  style={rowStyle} >
                    <Col span={6} style={colStyle}> Phone Number: </Col>
                    <Col span={12}> {loggedUser.phone} </Col>
                </Row>
                <Row style={rowStyle} >
                    <Col span={6} style={colStyle}> Email: </Col>
                    <Col span={12}> {loggedUser.email} </Col>
                </Row>
                <Row style={rowStyle} >
                    <Col span={6} style={colStyle}> Password: </Col>
                    <Col span={12}> {loggedUser.password} </Col>
                </Row>
            </Card>

            <Card title="Photo" style={{width : 400 , textAlign : 'center' }}>
                <Card  hoverable  style={{ width: 250 , marginLeft : 50}} >
                    <Image width={200} height={200} src={dp}
                    fallback="data:image/png;base64,"
                    />  <br /> <br />
                    <Meta title={name} style={{textAlign : 'center'}} />
                </Card>
            </Card>
        </Space>
    )
}